.dino-game {
    width: 600px;
    height: 200px;
    border-bottom: 1px solid #000;
    margin: auto;
}

.dino {
    width: 50px;
    height: 50px;
    background-image: url(../images/dino.png);
    background-size: 50px 50px;
    position: relative;
    top: 150px;
}

.cactus {
    width: 20px;
    height: 40px;
    background-image: url(../images/cactus.png);
    background-size: 20px 40px;
    position: relative;
    top: 110px;
    left: 580px;
    animation: none;
}

@keyframes cactusMove {
    0% {
        left: 580px;
    }

    100% {
        left: -20px;
    }
}

.jump {
    animation: jump 0.55s linear;
}

@keyframes jump {
    0% {
        top: 150px;
    }

    25% {
        top: 110px;
    }

    50% {
        top: 70px;
    }

    75% {
        top: 110px;
    }

    100% {
        top: 150px;
    }
}